@import '_colors';
@import '_mixins';

body.token {

  section.main {
    background: url('../img/token_bg.jpeg');
    background-size: cover;
    background-position: center;
    min-height: 100vh;
    color: $white;
    font-weight: 500;
    .message_line {
      background: rgba(0,0,0,.3);
      display: inline-block;
      width: calc(100% + 30px);
      margin-top: 30vh;
      text-align: center;
      padding: 50px 0;
      .message {
        background: $lightblue;
        display: inline-block;
        width: 275px;
        border-radius: 5px;
        position: relative;
        span {
          display: block;
          text-transform: uppercase;
          font-size: 1em;
          letter-spacing: 0.07em;
          padding: 20px 0 0; }
        &::after {
          content: '';
          display: inline-block;
          width: 15px;
          height: 15px;
          background: $lightblue;
          position: relative;
          top: 10px;
          transform: rotate(45deg); } } }
    .message_subline {
      h2 {
        text-align: center;
        margin: 30px 0 10px; } } }

  section.services {
    @include section-border($white);
    background: $darkblue;
    color: $white;
    font-weight: 300;
    padding-bottom: 60px;
    h1 {
      text-align: center;
      font-size: 1.8em;
      line-height: 1.2em;
      letter-spacing: 0.05em;
      margin: 5px 0 40px; }
    > div {
      text-align: center;
      img {
        width: 40%;
        max-width: 173px;
        margin-bottom: 30px; }
      span {
        display: block;
        text-align: center;
        font-size: .95em;
        letter-spacing: 0.05em;
        line-height: 1.2em;
        margin-bottom: 40px;
        strong {
          color: $orange; } } } }

  section.bio {
    color: $lightbrown;
    .row {
      padding-bottom: 60px;
      img {
        max-width: 100vw;
        position: relative;
        top: -10px;
        //margin-bottom: -60px
        margin-left: -15px; }
      h2 {
        margin-top: 40px;
        text-transform: uppercase;
        font-weight: 300;
        font-size: 2em;
        letter-spacing: 0.05em;
        line-height: 1.15em;
        margin-bottom: 25px;
        text-align: center;
        span {
          font-weight: 600;
          font-size: inherit;
          letter-spacing: inherit; } }
      ul {
        @include list($lightblue, $lightblue);
        li::before {
          width: 14px;
          height: 14px;
          top: .5em; } }

      &:nth-child(even) {
        background: url('../img/support_bg.png');
        ul {
          @include list($orange, $orange);
          li::before {
            width: 14px;
            height: 14px;
            top: .5em; } } } } }

  @media all and (min-width: 375px) {
    section.bio {
      .row {
        h2 {
          .wrap {
            display: none; } } } } }

  @media all and (min-width: 768px) {
    section.main {
      background-size: cover;
      background-position: center;
      .message_line {
        margin-top: 28vh;
        text-align: center;
        padding: 60px 0;
        .message {
          width: 600px;
          border-radius: 5px;
          span {
            font-size: 1.8em;
            letter-spacing: 0.09em;
            padding: 30px 0 10px;
            &::after {
              top: 15px; } } } }
      .url {
        a:link, a:visited {
          margin: 30px 0;
          font-size: 2.1em;
          letter-spacing: 0.09em; } } }

    section.services {
      padding: 0 60px 60px;
      h1 {
        font-size: 2.6em;
        letter-spacing: 0.1em;
        margin: 25px 0 50px;
        br {
          display: none; } }
      .row {
        > div {
          span {
            font-size: 1.2em; } } } }

    section.bio {
      .row {
        padding: 30px 40px 60px 80px;
        &:nth-child(odd) {
          > div {
            float: right; } }
        img {
          top: -30px;
          max-width: 100%; }
        h2 {
          margin: 70px 0; }
        ul {
          font-size: 1.2em; } } } } }
